"use client";

import { useEffect, useState } from "react";
import api from "@/lib/axios";
import Link from "next/link";
import {
  Store,
  MapPin,
  Phone,
  Utensils,
  Trash2,
  Pencil
} from "lucide-react";

export default function AdminManageRestaurants() {
  const [restaurants, setRestaurants] = useState<any[]>([]);
  const [loading, setLoading] = useState(true);

  useEffect(() => {
    const fetchRestaurants = async () => {
      try {
        const res = await api.get("/restaurants");
        setRestaurants(res.data);
      } catch (err) {
        console.error("Error loading restaurants", err);
      } finally {
        setLoading(false);
      }
    };

    fetchRestaurants();
  }, []);

  const deleteRestaurant = async (id: string) => {
    if (!confirm("Are you sure you want to delete this restaurant?")) return;

    try {
      await api.delete(`/restaurants/${id}`);
      setRestaurants(prev => prev.filter(r => r.id !== id));
    } catch (err) {
      alert("Failed to delete restaurant");
    }
  };

  if (loading) return <p className="p-10 text-xl">Loading...</p>;

  return (
    <div className="max-w-7xl mx-auto p-6">
      {/* Header */}
      <div className="flex justify-between items-center mb-8">
        <h1 className="text-4xl font-bold text-black">
          Manage Restaurants
        </h1>

        <Link
          href="/dashboard/admin/add-restaurant"
          className="bg-orange-600 text-white px-6 py-3 rounded-xl font-semibold hover:bg-orange-700"
        >
          + Add Restaurant
        </Link>
      </div>

      {/* Empty State */}
      {restaurants.length === 0 && (
        <p className="text-gray-600 text-lg">
          No restaurants found.
        </p>
      )}

      {/* Restaurants Grid */}
      <div className="grid md:grid-cols-2 lg:grid-cols-3 gap-6">
        {restaurants.map((r) => (
          <div
            key={r.id}
            className="bg-gradient-to-br from-orange-50 to-white p-6 rounded-2xl border shadow-md"
          >
            <div className="flex items-center gap-2 mb-3">
              <Store className="text-orange-600" />
              <h2 className="text-2xl font-bold text-black">
                {r.name}
              </h2>
            </div>

            <p className="text-gray-700 mb-4">
              {r.description}
            </p>

            <div className="space-y-2 text-black">
              <div className="flex items-center gap-2">
                <MapPin className="text-orange-600 w-4 h-4" />
                <span>{r.location}</span>
              </div>

              <div className="flex items-center gap-2">
                <Phone className="text-orange-600 w-4 h-4" />
                <span>{r.phone}</span>
              </div>

              <div>
                <strong>Country:</strong> {r.country}
              </div>

              <div>
                <strong>Cuisine:</strong> {r.cuisineType}
              </div>
               <div>
    <strong>Manager:</strong>{" "}
    <span className="text-orange-600 font-semibold">
      {r.manager?.name || "Not Assigned"}
    </span>
  </div>
            </div>

            {/* Actions */}
            <div className="flex gap-3 mt-6">
              <Link
                href={`/restaurant/edit/${r.id}`}
                className="flex-1 flex items-center justify-center gap-2 bg-orange-600 text-white py-2 rounded-lg hover:bg-orange-700"
              >
                <Pencil size={16} /> Edit
              </Link>

              <button
                onClick={() => deleteRestaurant(r.id)}
                className="flex-1 flex items-center justify-center gap-2 bg-red-600 text-white py-2 rounded-lg hover:bg-red-700"
              >
                <Trash2 size={16} /> Delete
              </button>
            </div>
          </div>
        ))}
      </div>
    </div>
  );
}
